import OpenAI from 'openai';

declare global {
  interface Window {
    config: {
      OPENAI_API_KEY: string;
    };
  }
}

const getApiKey = () => {
  return window.config?.OPENAI_API_KEY;
};

export const setApiKey = (key: string) => {
  if (!key.startsWith('sk-')) {
    throw new Error('Invalid API key format');
  }
  // userApiKey = key;
};

export const hasValidApiKey = () => {
  return !!getApiKey();
};

export const transcribeAudio = async (audioBlob: Blob): Promise<string> => {
  try {
    const apiKey = getApiKey();
    if (!apiKey) {
      throw new Error('OpenAI API key is missing! Check your config.js file');
    }

    // Create FormData
    const formData = new FormData();
    formData.append('file', audioBlob, 'audio.webm');
    formData.append('model', 'whisper-1');
    formData.append('language', 'en');

    console.log('Sending audio to OpenAI:', {
      blobSize: audioBlob.size,
      blobType: audioBlob.type
    });

    // Make the API call
    const response = await fetch('https://api.openai.com/v1/audio/transcriptions', {
      method: 'POST',
      headers: {
        'Authorization': `Bearer ${apiKey}`
      },
      body: formData
    });

    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(`OpenAI API error: ${errorData.error?.message || response.statusText}`);
    }

    const data = await response.json();
    return data.text;
  } catch (error) {
    console.error('Transcription error:', error);
    throw error;
  }
};

export const generateSummary = async (transcript: string): Promise<string> => {
  try {
    if (!transcript.trim()) {
      return "No transcript available to summarize.";
    }

    const apiKey = getApiKey();
    if (!apiKey) {
      throw new Error('OpenAI API key is missing! Check your config.js file');
    }

    const response = await fetch('https://api.openai.com/v1/chat/completions', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${apiKey}`
      },
      body: JSON.stringify({
        model: "gpt-3.5-turbo",
        messages: [
          {
            role: "system",
            content: "Create a concise bullet-point summary of the following transcript. Focus on key points and action items."
          },
          {
            role: "user",
            content: transcript
          }
        ],
        temperature: 0.7,
        max_tokens: 500
      })
    });

    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(`OpenAI API error: ${errorData.error?.message || response.statusText}`);
    }

    const data = await response.json();
    return data.choices[0].message.content;
  } catch (error) {
    console.error('Summary generation error:', error);
    throw error;
  }
}; 